









































import { defineComponent, ref } from "@vue/composition-api";

export default defineComponent({
  name: "IndustryList",

  props: {
    industries: Object
  },

  setup(props) {
    const selectedItem = ref([] as Array<number>);
    const items = ref(props.industries);

    return {
      selectedItem,
      items
    };
  }
});
